.table {
	$self: &;

	font-size: 14px;
	line-height: 1.2;

	thead {
		th {
			padding-top: 15px;
			padding-bottom: 15px; } }

	&-nowrap {
		th,
		td {
			&:not(.no-wrap) {
				white-space: nowrap; } } }

	// Card table
	&.card-table {
		color: $gray-600;

		th,
		td {
			&:first-child {
				padding-left: 25px;

				&:last-child {
					padding-right: 25px; } } }

		thead {
			th {
				padding-top: 18px;
				padding-bottom: 18px;
				font-size: 12px;
				text-transform: uppercase;
				font-weight: 600;
				color: $gray-600;
				vertical-align: middle;
				white-space: nowrap; } }

		tbody {
			tr {
				&:first-child {
					td {
						padding-top: 17px; } }

				&:last-child {
					td {
						padding-bottom: 17px; } } } }

		#{$self} {
			&-cell {
				&-check {
					width: 4%;
					min-width: 50px; }

				&-title {
					width: 15%;

					&-link {
						color: $dark;
						text-decoration: none;
						font-weight: 600;

						@include media-breakpoint-up(lg) {
							&:hover {
								color: $primary; } } } }

				&-desc {
					width: 30%;
					min-width: 450px; }

				&-action {
					width: 8%;
					min-width: 140px; } } } } }
