.btn {
    align-items: center;
    display: inline-flex;
    justify-content: center;
    line-height: normal;

    &:hover {
        border-color: transparent; }

    &-round {
        border-radius: 50%; }

    &-primary,
    &-success,
    &-danger {
        color: $white !important; }

    &-outline-primary,
    &-outline-danger,
    &-outline-success {
        &:hover {
            color: $white; } } }
